.desktop-menu {
    background-color: lightgray;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 20px 0;

    .desktop-menu-section {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        a {
            margin: 0px 20px;
            text-decoration: none;
            color: black;
        }
    }

    .desktop-menu-logo {
        img {
            max-width: 100px;
            height: auto;
        }
    }

    .desktop-menu-nav {
        display: flex;

        .desktop-menu-nav-item {
            margin: 0 10px;
            position: relative;

            a:before {
                content: "";
                position: absolute;
                background-color: black;
                bottom: -3px;
                left: 0;
                width: 100%;
                height: 2px;
                visibility: hidden;
                transform: scaleX(0);
                transition: all 0.3s ease-in-out;
            }

            a:hover:before {
                visibility: visible;
                transform: scaleX(1);
            }
        }
    }
}